___CSS_LOADER_EXPORT___.push([module.id, `/* Body */
body {
  font-family: "Roboto", Arial, Verdana, Helvetica, sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

* {
  box-sizing: border-box;
}

/* Layout */
.container-fluid {
  max-width: 100vw;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

.header {
  position: relative;
  padding: 1.5rem 3rem;
  background-color: #e8eaf6;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
  z-index: 1;
}

.container {
  background-color: #fbfbfb;
  padding: 5rem 0;
}

.footer {
  position: relative;
  padding: 1.25rem 0;
  background-color: #e8eaf6;
  text-align: center;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
  z-index: 1;
}

/* Typography */
.page-title {
  font-size: 2rem;
  margin: 0;
  padding: 0;
}

.card-title {
  font-size: 4rem;
  font-weight: bold;
}

.card-subtitle {
  font-size: 1.5rem;
  font-weight: bold;
}

.card-text {
  font-weight: normal;
  font-size: 1rem;
}

.fw-bold {
  font-weight: bold;
}

.text-muted {
  color: #6c757d;
}

.text-primary {
  color: #3b71ca;
}

.text-info {
  color: #54b4d3;
}

.text-warning {
  color: #e4a11b;
}

/* Spacing */
.me-1 {
  margin-right: 0.25rem;
}
.me-2 {
  margin-right: 0.5rem;
}
.me-3 {
  margin-right: 1rem;
}
.me-4 {
  margin-right: 1.5rem;
}
.me-5 {
  margin-right: 2rem;
}

.mb-1 {
  margin-bottom: 0.25rem;
}
.mb-2 {
  margin-bottom: 0.5rem;
}
.mb-3 {
  margin-bottom: 1rem;
}

.mt-auto {
  margin-top: auto;
}

.mb-auto {
  margin-bottom: auto;
}

.me-auto {
  margin-right: auto;
}

/* Form */
.form-wrapper {
  position: relative;
  width: max(500px, 30%);
  display: flex;
  flex-flow: column wrap;
  padding: 2rem 3rem;
  margin: 0 auto 3rem;
  background-color: #e8eaf6;
  border-radius: 1rem;
  gap: 2rem;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.3);
  z-index: 2;
}

.form-row {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  position: relative;
  padding: 1rem 0 0;
  margin-top: 0.7rem;
  width: 100%;
}

.form-row .error {
  display: inline-block;
  color: #d50000;
  font-size: 1rem;
  font-weight: normal;
  padding: 0;
}

.form-row .error.active {
  padding: 0.25rem 0.5rem;
}

.form-control {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  color: #2962ff;
  padding: 0.5rem;
  background: transparent;
  transition: border-color 0.3s;
}

.form-control::placeholder {
  color: transparent;
}

.form-control:placeholder-shown ~ .form-label {
  font-size: 1.3rem;
  cursor: text;
  top: 1.5rem;
  left: 0.5rem;
}

.form-label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.3s;
  font-size: 1rem;
  color: #9b9b9b;
}

.form-control:valid {
  border-image: linear-gradient(to right, #11998e, #38ef7d);
}

.form-control:invalid {
  border-image: linear-gradient(to right, #d50000, #ff8a80);
}

.form-control.valid {
  border-width: 3px;
  border-image: linear-gradient(to right, #11998e, #38ef7d);
  border-image-slice: 1;
}

.form-control.invalid {
  border-width: 3px;
  border-image: linear-gradient(to right, #d50000, #ff8a80);
  border-image-slice: 1;
}

.form-control:focus {
  border-image: linear-gradient(to right, #2962ff, #82b1ff);
}

.form-control:focus,
.form-control:valid,
.form-control:invalid {
  padding-bottom: 0.5rem;
  font-weight: bold;
  border-width: 3px;
  border-image-slice: 1;
}

.form-control:placeholder-shown {
  border: 0;
  border-bottom: 2px solid #9b9b9b;
}

.form-control:focus:placeholder-shown {
  border-width: 3px;
  border-image: linear-gradient(to right, #2962ff, #82b1ff);
  border-image-slice: 1;
}

.form-control.valid:placeholder-shown {
  border-width: 3px;
  border-image: linear-gradient(to right, #11998e, #38ef7d);
  border-image-slice: 1;
}

.form-control.invalid:placeholder-shown {
  border-width: 3px;
  border-image: linear-gradient(to right, #d50000, #ff8a80);
  border-image-slice: 1;
}

/* reset input */
.form-control:required,
.form-control:invalid {
  box-shadow: none;
}

.btn-submit {
  display: inline-flex;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 auto;
  background-color: #2962ff;
  color: #fff;
  transition: background-color 0.3s, color 0.3s, box-shadow 0.3s;
}

.btn-submit:hover,
.btn-submit:active {
  background-color: #0039cb;
  color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

/* Card */
.card {
  background-color: #e8eaf6;
  padding: 3rem 4rem;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
}

.card-header {
  display: flex;
  flex-direction: column;
  margin: 0 auto 2rem;
  text-align: center;
}

.card-body {
  display: flex;
  flex-flow: row wrap;
  flex-grow: 1;
  justify-content: space-evenly;
  gap: 4rem;
}

.card-row {
  display: flex;
  flex-direction: row;
  height: 100%;
}

.card-col {
  display: flex;
  flex-direction: column;
}

.weather-main-icon {
  height: 5rem;
  width: auto;
}

.weather-icon {
  font-size: 3rem;
}

.card-item {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 200px;
  align-items: center;
  gap: 0.5rem;
}

/* Footer */
.footer-text {
  font-size: 1rem;
  font-weight: normal;
  color: #757575;
}

/* Animation */
.btn-submit {
  position: relative;
  overflow: hidden;
}

.btn-submit span {
  position: absolute;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.8);
  width: 100px;
  height: 100px;
  margin-top: -50px;
  margin-left: -50px;
  animation: ripple 1s;
  opacity: 0;
}

/* Add animation */
@keyframes ripple {
  from {
    opacity: 1;
    transform: scale(0);
  }

  to {
    opacity: 0;
    transform: scale(10);
  }
}
